import { useState, useEffect } from "react";
import PropTypes from "prop-types";
import movieApi from "../../services/moviesApi";
import NoImage from "../../images/NoImage.jpg";
import styles from "../Cast/cast.module.css";

export default function Cast({ id }) {
  const [actors, setActors] = useState(null);

  useEffect(() => {
    movieApi
      .fetchMoviesCast(id)
      .then((data) => {
        setActors(data.cast);
      })
      .catch((error) => {
        console.log(error);
      });
  }, [id]);
  return (
    <ul className={styles.castList}>
      {actors &&
        actors.map(({ id, original_name, character, profile_path }) => (
          <li key={id} className={styles.cardItem}>
            <img
              src={
                profile_path
                  ? `http://image.tmdb.org/t/p/w500/${profile_path}`
                  : NoImage
              }
              alt={original_name}
            />
            <p className={styles.name}>{original_name}</p>
            <p className={styles.character}>{character}</p>
          </li>
        ))}
    </ul>
  );
}
Cast.propTypes = {
  id: PropTypes.string.isRequired,
};
